.btn-style-1 {
    background-color: var(--color-blue);
    border-radius: 2em;
    border: 1px var(--color-blue) solid;
    color: white;
    padding: 0.5em 2em;
    display: inline-block;
    transition-duration: 0.3s;
    &:hover {
        border-color: black;
        color: black;
        background-color: white;
    }
}

.btn-style-2 {
    background-color: white;
    border-radius: 2em;
    border: 1px black solid;
    padding: 0.5em 2em;
    display: inline-block;
    transition-duration: 0.3s;
    &:hover {
        border-color: var(--color-blue);
        color: white;
        background-color: var(--color-blue);
    }
}

.app-box-shadow {
    box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.3);
}

.app-list-item {
    &::before {
        display: inline-block;
        content: "";
        width: 0.6em;
        height: 0.6em;
        background-color: var(--color-blue);
        margin-right: 0.5em;
    }
}
